import { ref, defineComponent, useContext, computed } from '@nuxtjs/composition-api';
export default defineComponent({
    layout: 'menu',
    setup() {
        const { app, $vuetify } = useContext();
        const currentTheme = ref($vuetify.theme.dark ? 'dark' : 'light');
        const colors = computed(() => {
            return Object.keys($vuetify.theme.themes[currentTheme.value]);
        });
        const colorHexes = computed(() => {
            return colors.value
                .filter((i) => !i.startsWith('on'))
                .map((i) => {
                return {
                    text: i,
                    color: $vuetify.theme.themes[currentTheme.value][i],
                    onColor: $vuetify.theme.themes[currentTheme.value][`on${i.charAt(0).toUpperCase()}${i.slice(1)}`]
                };
            });
        });
        return {
            app,
            colors,
            colorHexes,
            currentTheme
        };
    }
});
